
.about-us {
  font-family: $second-font;
  color: $heading-text-color;
  padding: 84px 31px;

  @media screen and (min-width: $tablet) {
    display: flex;
    padding: 100px 39px;
  }

  @media screen and (min-width: $desktop) {
    padding: 130px 15px;

  }
}

.about-us__list {

  @media screen and (max-width: $tablet - 1px) {
    display: none;
  }

  @media screen and (min-width: $desktop) {

    display: flex;
  }
}

.about-us__item:not(:last-child) {

  @media screen and (max-width: $desktop - 1px) {
    margin-bottom: 30px;
  }

  @media screen and (min-width: $desktop) {

    margin-right: 30px;
  }
}

.about-us__content {

  @media screen and (min-width: $tablet) {

    width: 330px;
    margin-left: 30px;
  }


  @media screen and (min-width: $desktop) {

    width: 500px;
    margin-left: 64px;
  }
}


.about-us__description {

  display: flex;
  align-items: center;
  font-weight: normal;
  font-size: 11px;
  line-height: 1.36;
  letter-spacing: 0.1em;
  text-transform: uppercase;


  &::before {
    @extend %line-before-black;
  }
}

.about-us__title {

  width: 330px;
  margin-top: 20px;
  font-family: $main-font;
  font-size: 28px;
  line-height: 1.18;
  letter-spacing: 0.05em;


  @media screen and (min-width: $desktop) {

    width: 500px;
    font-size: 42px;
    line-height: 1.17;
  }
}

.about-us__label {
  margin-top: 20px;
  font-size: 16px;
  line-height: 1.62;
  letter-spacing: 0.02em;
  color: $primaty-text-color;


  @media screen and (min-width: $desktop) {

    font-size: 18px;
    line-height: 1.67;
  }
}

.about-us__text {
  margin-top: 20px;
  margin-bottom: 40px;
  font-size: 14px;
  line-height: 1.86;
  letter-spacing: 0.02em;
  color: $primaty-text-color;
}

.about-us__btn {
  @extend %button;
  width: 160px;
  height: 40px;
  background-color: transparent;
  color: $heading-text-color;

  &:hover,
  &:focus {
    background-color: $accent-color;
    color: $white;
  }
}
