.logo {
  position: absolute;
  top: 40px;
  left: 30px;
  z-index: 4;

  display: inline-block;

  color: $white;

  @media screen and (min-width: 768px) {
    top: 32px;
    left: 50px;

    color: $heading-text-color;
  }


  @media screen and (min-width: 1200px) {
    left: 15px;

  }
}
